import axios from "axios";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Modal, Button } from 'react-bootstrap';
import {notify} from 'react-notify-toast';

function List(){
    const [showAddModal, setShowAddModal] = useState(false);
    const [userData, setUserData] = useState({users: []});
    const [isLoading, setIsLoading] = useState(false);

    const getusers = () => {
        axios.get('/user', {
        }).then(response => {
            if(response.data){
                setUserData(response.data.data);
            }
        }).catch(error => {
            console.log(error);
        });
    }

    const handleAddModalShow = (e) => {
        e.preventDefault();
        setShowAddModal(true);
    }

    const handleAddModalClose = (e) => {
        e.preventDefault();
        setShowAddModal(false);
    }

    const invokeAdd = e => {
        setIsLoading(true);
        e.preventDefault();
        const params = {
            first_name: e.target.first_name.value,
            last_name: e.target.last_name.value,
            introduction: e.target.introduction.value,
            email: e.target.email.value,
            phone_code: e.target.phone_code.value,
            phone: e.target.phone.value,
            experience: e.target.experience.value,
            achievements: e.target.achievements.value
        };
        axios.post('/user', params).then(response => {
            if(response.data.data){
                notify.show("Successfully updated", 'success');
                setIsLoading(false);
                setShowAddModal(false);
            }else{
                notify.show(response.data.message, 'error');
                setIsLoading(false);
                setShowAddModal(false);
            }
        }).catch(error => {
            notify.show("Sorry, Something went wrong", 'error');
            setIsLoading(false);
            setShowAddModal(false);
        });
    }

    useEffect(() => {
        getusers();
    }, [isLoading]);

    return (
        <div class="container-xl">
            <div class="table-responsive">
                <div class="table-wrapper">
                    <div class="table-title">
                        <div class="row">
                            <div class="col-sm-5">
                                <h2>User <b>Management</b></h2>
                            </div>
                            <div class="col-sm-7">
                                <a href="#" class="btn btn-secondary" onClick={handleAddModalShow}><span>Add New User</span></a>				
                            </div>
                        </div>
                    </div>
                    <table class="table table-striped table-hover">
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>Name</th>
                                <th>Email</th>
                            </tr>
                        </thead>
                        <tbody>
                            {userData.users.map(user => {
                                return(
                                    <tr key={user.id}>
                                        <td>{user.id}</td>
                                        <td><Link to={"/user/"+user.id}>{user.first_name+' '+user.last_name}</Link></td>
                                        <td><Link to={"/user/"+user.id}>{user.email}</Link></td>
                                    </tr>
                                )
                            }) }
                            <p hidden={userData.users.length > 0}>No records found</p>
                        </tbody>
                    </table>
                </div>
            </div>
                {/* Add modal */}
                <Modal
                    show={showAddModal}
                    onHide={handleAddModalClose}
                    backdrop="static"
                    keyboard={false}
                >
                    <form onSubmit={invokeAdd}>
                    <Modal.Header>
                        <Modal.Title>Add new user</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                                <label for="first_name" className="sr-only">First name</label>
                                <input type="text" id="first_name" name="first_name" className="form-control" placeholder="First name" required autofocus />

                                <label for="last_name" className="sr-only">Last name</label>
                                <input type="text" id="name" name="last_name" className="form-control" placeholder="Last name" required autofocus />

                                <label for="introduction" className="sr-only">Introduction</label>
                                <textarea className="form-control" name="introduction" rows="5" id="introduction" placeholder="Introduction" required autofocus></textarea>

                                <label for="email" className="sr-only">Email</label>
                                <input type="email" id="email" name="email" className="form-control" placeholder="Email" required autofocus />

                                <select className="form-control" id="phone_code" name="phone_code" required autofocus>
                                    <option value="" selected disabled>Phone code</option>
                                    <option value="+1">+1</option>
                                    <option value="+91">+91</option>
                                </select>

                                <label for="phoneCode" className="sr-only">Phone</label>
                                <input type="text" id="phone" name="phone" className="form-control" placeholder="Phone" pattern="\d*" maxLength="10" required autofocus />

                                <label for="experience" className="sr-only">Experience(in Years)</label>
                                <input type="number" id="experience" name="experience" className="form-control" placeholder="Experience(in Years)" pattern="\d*" autofocus />

                                <label for="address" className="sr-only">Achievements</label>
                                <textarea className="form-control" name="achievements" rows="5" id="achievements" placeholder="Achievements" autofocus></textarea>
                        </Modal.Body>
                        <Modal.Footer>
                        <Button variant="secondary" onClick={handleAddModalClose}>
                            Cancel
                        </Button>
                                <Button variant="primary" type="submit" disabled={isLoading}>{ isLoading ? 'Loading...' : 'Add' }</Button>
                        </Modal.Footer>
                        </form>
                    </Modal>
                    {/* Add modal */}
        </div>
    )
}


export default List;