import axios from "axios";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";

function Detail(props){
    const [userData, setUserData] = useState({user: {}});

    const getUser = () => {
        const { id } = props.match.params;
        axios.get('/user/'+id, {
        }).then(response => {
            if(response.data){
                setUserData(response.data.data);
            }
        }).catch(error => {
            console.log(error);
        });
    }

    useEffect(() => {
        getUser();
    }, []);

    return (
        <div class="container-xl">
            <div class="table-responsive">
                <div class="table-wrapper">
                    <div class="table-title">
                        <div class="row">
                            <div class="col-sm-5">
                                <h2>User <b>Detail</b></h2>
                            </div>
                            <div class="col-sm-7">
                                <Link className="btn btn-secondary" to="/">Back</Link>			
                            </div>
                        </div>
                    </div>
                    <table class="table table-striped table-hover">
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>Name</th>
                                <th>Introduction</th>
                                <th>Email</th>
                                <th>Phone</th>
                                <th>Experience</th>
                                <th>Achievements</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>{userData.user ? userData.user.id : ''}</td>
                                <td>{userData.user ? userData.user.first_name+' '+userData.user.last_name : ''}</td>
                                <td>{userData.user ? userData.user.introduction : ''}</td>
                                <td>{userData.user ? userData.user.email : ''}</td>
                                <td>{userData.user ? userData.user.phone_code+userData.user.phone : ''}</td>
                                <td>{userData.user ? userData.user.experience : ''}</td>
                                <td>{userData.user ? userData.user.achievements : ''}</td>
                            </tr>
                            <p hidden={userData.user.id}>No details found</p>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}


export default Detail;